<script>
import moment from "moment";
import _ from "lodash";
import axios from "axios";
import Swal from "sweetalert2";

export default {
  name: "CusDashboardView",
  props: ["dataHutang", "ipAdress"],

  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      info_pembayaran_hutang: false,
      info_pembayaran_d_saldo: false,
      jumlahDepositSaldo: null,
    };
  },

  methods: {
    tarikCoin() {
      axios
        .post(
          this.ipAdress +
            "api/transaksi/tarik-coin?username=" +
            this.user.username
        )
        .then((response) => {
          Swal.fire({
            icon: response.data.status,
            title: response.data.message,
            text: response.data.keterangan,
            showConfirmButton: false,
            timer: 1500,
          }).then(function () {
            window.location = "/customer/dashboard";
          });
        });
    },
    numberFormat(x) {
      if (x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      } else {
        return (x = "0");
      }
    },
    format_date(value) {
      if (value) {
        return moment(String(value)).format("DD MMMM YYYY HH:mm");
      }
    },
  },

  // mounted() {
  //   console.log(this.user);
  // },

  computed: {
    orderedDataHutang: function () {
      return _.orderBy(this.dataHutang.data_hutang, "created_at", "desc");
    },
  },
};
</script>

